@use "../foundation/variables" as *;
@use "../foundation/mixins" as *;

// 下層共通ヘッダー

.c-mv{
    width: 100%;
    height: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;

    &__title{
        background-color: rgba($color-secondary-dark, 0.6);
        min-width: 478px;
        padding-block: 15px;
        padding-inline: 1em;

        &-text{
            padding-block: 29px;
            text-align: center;
            font-size: rem(38);
            letter-spacing: 0.2em;
            color:$color-white;
            border-block: solid 1px $color-white;
            line-height: 1;
        }
    }

    @include respond(phone){
        height: auto;
        aspect-ratio: 1/1;

        &__title{
            min-width: 257px;
            padding-block: 8px;
    
            &-text{
                padding-block: 18px;
                font-size: rem(26);
            }
        }
    }
}

// lightbox

.c-gallery{
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
    position: relative;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__text{
        position: absolute;
        bottom: 0;
        width: 100%;
        padding-block: calc(9px + var(--lending-trim));
        padding-inline: 11px;
        line-height: 1.5;
        font-size: rem(17);
        background-color: rgba(0, 0, 0, 0.4);
        color: $color-white;
    }

}

//レスポンシブ背景ブロック
.wp-block-lazyblock-responsive-bg{
    position: relative;
    &__img{
        position: absolute;
    }
}