$color-text: #231815;
$color-text-gray: #4D4D4D;
$color-primary-xlight: #F2F2F2;
$color-primary-light: #D5E3D4;
$color-primary: #799E7C;
$color-primary-dark: #6E9072;
$color-secondary: #DFD7CB;
$color-secondary-light: #F0EDE6;
$color-secondary-dark: #ABA092;
$color-gray: #808080;
$color-gray-dark: #4D4D4D;
$color-gray-light: #dcdcdc;
$color-white: #ffffff;


:root {
    --lending-trim: calc((1em - 1lh) / 2);
}

@supports not (top: 1lh) {
    :root {
        --lending-trim: 0;
    }
}
